.header{position:fixed;width:100%;z-index:999;border-bottom:.3px solid #ccc;background-color:#fff}.header-background{background-color:var(--primary-color)}.header-top{display:flex;justify-content:space-between;align-items:center;padding-top:.5rem;padding-bottom:.5rem;gap:2rem}.header-top .logo{color:#fff}.header-top .logo h1{font-size:20px;margin-bottom:unset}.header-top .logo h1 span{font-weight:bold}.header-top .search-bar{display:flex;flex-grow:1}.header-top .search-bar input{flex:1;padding:10px;border:none;border-radius:var(--border-radius-small) 0 0 var(--border-radius-small);outline:none}.header-top .search-bar button{background-color:var(--secondary-color);border:none;padding:10px;border-radius:0 var(--border-radius-small) var(--border-radius-small) 0;cursor:pointer}.header-top ul{padding-left:unset;margin-bottom:unset;display:flex;justify-content:center;align-items:center}.header-top ul a{text-decoration:unset}.header-top ul svg{color:#fff}.header-top .customer-btn{background-color:#fff;border-radius:999px;padding:.2rem .5rem;max-width:120px;overflow-x:hidden;white-space:nowrap;text-overflow:ellipsis}.header-bottom{background-color:var(--secondary-color);display:flex;justify-content:space-between;align-items:center;font-size:14px}.header-bottom ul{display:flex;list-style:none;gap:20px;margin-bottom:unset;align-items:center;padding-left:unset}.header-bottom ul a{text-decoration:none;color:var(--primary-color)}.header-bottom ul li{padding:.5rem 0rem;cursor:pointer}.header-bottom li:has(.submenu):hover .submenu{display:block}.header-bottom .submenu{display:none;position:relative}.header-bottom .submenu ul{display:block;padding:.5rem;width:100%;position:absolute;background-color:#fff;border-radius:var(--border-radius-small);box-shadow:rgba(0,0,0,.1) 0px 4px 12px;z-index:99}.header-bottom .submenu ul li{border-bottom:.3px solid #ccc}.header-bottom .submenu ul li:last-child{border-bottom:none}.header-bottom .submenu ul li:hover{background-color:#ccc}.header-bottom .collection{display:flex;border-radius:var(--border-radius-small);border:#ccc solid 1px;padding:.5rem;justify-content:center;align-items:center;gap:.5rem}.header-bottom .collection__title{text-decoration:none;color:var(--primary-color)}.header-bottom .collection__icon{color:var(--primary-color)}.header-bottom .hotline{display:flex;gap:.5rem}.header-bottom .hotline svg{color:var(--primary-color)}.header-bottom .hotline span{font-size:medium;font-weight:400}@media(max-width: 1020px){.header-top .logo,.header-top .icons{display:none}.header-top .search-bar{margin:unset}.header-bottom{padding:0rem 1rem}.header-bottom .hotline{display:none}}